<template>
    <div class="btn-container">
        <div class="btn download">
            <span @click="goApp">App内下载</span>
        </div>
        <div class="btn save">
            <span @click="saveToCloud">转存到网盘</span>
        </div>
    </div>
</template>

<script>
export default {
  name: "saveButton",
  props: {
  },
  computed: {
  },
  methods: {
    saveToCloud() {
      this.$emit("saveToCloud");
    },
    goApp() {
      this.$emit("goApp");
    }
  }
};
</script>

<style scoped>
    .btn-container {
        position: fixed;
        width: 80%;
        z-index: 1;
        bottom: 40px;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
      }
      .btn {
          width: 100px;
          height: 40px;
          line-height: 40px;
          font-size: 18px;
          border: 1px solid blue;
          border-radius: 10px;
          text-align: center;
      }
      .download {
            background-color: #fff;
            color: blue;
            box-shadow: 0 6px 12px rgba(0,0,0,0.5);
        }
        .save {
            background-color: blue;
            color: #fff;
            box-shadow: 0 6px 12px rgba(94,136,155,0.8);
        }
</style>

  </div>
</template>



