<template>
    <div class="login">
        name: <input ref="name" type="text" placeholder="name" @input="handleNameInput"><br/>
        pass: <input ref="pass" type="password" placeholder="pass" @input="handlePassInput">
        <div class="log">
            <p>name is : {{name}}</p>
            <p>pass is : {{pass}}</p>
        </div>
        <button class="click" @click="clear">clear</button>
    </div>
</template>
<script>
export default {
    data() {
        return {
            name: '',
            pass: ''
        }
    },
    mounted() {
        const name = this.$route.query.name
        console.log(`name is ${name}`)
    },
    methods: {
        handleNameInput(e) {
           this.name = e.target.value
        },
        handlePassInput(e) {
            this.pass = e.target.value
        },
        clear() {
            this.$refs.name.value = ''
            this.$refs.pass.value = ''
        }
    }
}
</script>


