import Router from "vue-router"
import Vue from 'vue'
import Home from '../pages/Home'
import Login from '../pages/login'
Vue.use(Router)

const routes  = [
    {
        path: '/',
        component: Home
    },
    {
        path: '/home',
        component: Home
    },
    {
        path: '/login',
        component: Login
    }
]

export default new Router({
    routes
})


